.typewriter p {
    overflow: hidden;
    white-space: nowrap;
    margin: 0 auto;
    -webkit-animation:
            typing 3.5s steps(40, end),
            blink-caret .75s step-end infinite;
            animation:
            typing 3.5s steps(40, end),
            blink-caret .75s step-end infinite;
}


@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
}


@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@-webkit-keyframes bounceInUp {
    from,
    60%,
    75%,
    90%,
    to {
        -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 0.5);
                animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 0.5);
    }

    from {
        opacity: 0;
        transform: translate3d(0, 3000px, 0) scaleY(5);
    }

    60% {
        opacity: 1;
        transform: translate3d(0, -20px, 0) scaleY(0.9);
    }

    75% {
        transform: translate3d(0, 10px, 0) scaleY(0.95);
    }

    90% {
        transform: translate3d(0, -5px, 0) scaleY(0.985);
    }

    to {
        transform: translate3d(0, 0, 0);
    }
}

@keyframes bounceInUp {
    from,
    60%,
    75%,
    90%,
    to {
        -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 0.5);
                animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 0.5);
    }

    from {
        opacity: 0;
        transform: translate3d(0, 3000px, 0) scaleY(5);
    }

    60% {
        opacity: 1;
        transform: translate3d(0, -20px, 0) scaleY(0.9);
    }

    75% {
        transform: translate3d(0, 10px, 0) scaleY(0.95);
    }

    90% {
        transform: translate3d(0, -5px, 0) scaleY(0.985);
    }

    to {
        transform: translate3d(0, 0, 0);
    }
}

.bounceInUp {
    -webkit-animation-name: bounceInUp;
            animation-name: bounceInUp;
}








#aboutme{
    background-color: #F8F8F8;
    width: 100vw;
    height: 90vh;

    display: grid;
    grid-template-columns: 50vw 50vw;
    grid-template-areas:
            "bio image";
    padding-top: 2vh;
}

.bioImage{
    grid-area: image;
    height: 100vh;
    width: 50vw;
    text-align: center;
    padding: 1vh;
}

.myImage{
    height: 72vh;
    width: 32vw;
}

.myName{
    font-family: 'Dancing Script', cursive;
    font-weight: 700;
    font-size: 2vw;
}

.bio {
    grid-area: bio;
    padding: 0 1vw 0 5vw;
}

.bioName {
    font-weight: 700;
    margin-bottom: 4vh;
    font-size: 2vw;
    margin-bottom: 1vh;
    font-family: "Caveat";
}

.bioDetails {
    display: grid;
    grid-template-row: 20vw 30vw 22vw;
    grid-column-gap: 2vw;
}

.bioDetail {
    font-size: 2.3vh;
    font-weight: 200;
    line-height: 1.3;
    font-family: "Open Sans";
}

.bioDetailTitle {
    font-family: "Caveat";
    font-weight: 600;
    font-size: 3vh;
    margin: 1.2vh 0 0 0;
    padding-top: 2.5vh;
}

@media only screen and (max-width: 450px) {
    #aboutme {
        height: 90vh;
        display: grid;
        grid-template-columns: 100vw;
        grid-template-rows: 40vh 60vh;
        grid-template-areas: "image" "bio";
    }

    .bioImage{
        grid-area: image;
        height: 50vh;
        width: 50vw;
        margin-left: auto;
        margin-right: auto;
    }

    .myImage{
        height: 35vh;
        width: 45vw;
    }

    .myName{
        font-family: 'Dancing Script', cursive;
        font-weight: 700;
        font-size: 3vw;
    }

    .bioName {
        font-weight: 700;
        margin-bottom: 4vh;
        font-size: 3vw;
        margin-bottom: 1vh;
        font-family: "Caveat";
        text-align: center;
    }

    .bioDetail {
        font-size: 1.8vh;
        font-weight: 200;
        line-height: 1.3;
        font-family: "Open Sans";
    }

    .bioDetailTitle {
        font-family: "Caveat";
        font-weight: 600;
        font-size: 2vh;
        margin: 1.2vh 0 0 0;
        padding-top: 2.5vh;
    }
}
